import React from 'react'
import intro from 'assets/image/intro.png'
import { IntroImgWrapper, IntroMsgWrapper, IntroTitle, IntroMsg, IntroBtn } from './styled'
import './intro.css'

export default function Intro() {
  return (
    <div className="intro-box">
      <IntroImgWrapper>
        <img src={intro} alt="intro"></img>
      </IntroImgWrapper>   
      <IntroMsgWrapper>
        <IntroTitle>企业简介</IntroTitle>
        <IntroMsg>汇聚智享成立于2020年，致力于为企业提供互联网支付、劳动力相关的整体科技解决方案，帮助企业一站式解决在市场营销、流量管理、资金收付、账户管理、财税合规等方面问题，助力企业全面提升流量管理、中后台管理、财务管理、资金管理效率，为企业提供持续的增长价值。</IntroMsg>
        <div style={{textAlign: 'right'}}>
          <IntroBtn>查看详情</IntroBtn>
        </div>
      </IntroMsgWrapper>
    </div>
  )
}
