import React, {useEffect} from 'react'
import {IconLeft, IconRight, NewsBox, NewsMsgWrapper, NewsSub, NewsTitle, NewsItemTitle, SwiperBox, NewsItemSub, NewsItemCon, NewsBack,} from './styled'

import Swiper, { Navigation } from 'swiper'
import 'swiper/swiper-bundle.css'
import news1 from 'assets/image/news1.png'
import news2 from 'assets/image/news2.png'
import news3 from 'assets/image/news3.png'
import iconL from 'assets/image/icon-left.png'
import iconR from 'assets/image/icon-right.png'
Swiper.use([Navigation])

export default function News() {
  useEffect(() => {
    new Swiper('.news-swiper-container', {
      slidesPerView: 3.1,
      navigation: {
        nextEl: '#icon-next',
        prevEl: '#icon-before',
      }
    })
  }, [])

  const news = [
    { img: news1, title: '标题1231231231231232', sub: '美国-纽约', content: '在128公路兴起和发展的大半个世纪中，其周边的麻省理工学院、哈佛大学和波士顿大学等著名学府扮演了极其重要的角色。其中由麻省理工' },
    { img: news2, title: '标题1231231231231232', sub: '美国-纽约', content: '在128公路兴起和发展的大半个世纪中，其周边的麻省理工学院、哈佛大学和波士顿大学等著名学府扮演了极其重要的角色。其中由麻省理工' },
    { img: news3, title: '标题1231231231231232', sub: '美国-纽约', content: '在128公路兴起和发展的大半个世纪中，其周边的麻省理工学院、哈佛大学和波士顿大学等著名学府扮演了极其重要的角色。其中由麻省理工' },
  ]
  return (
    <NewsBox>
      <NewsTitle>NEWS CENTER</NewsTitle>
      <NewsSub>新闻中心</NewsSub>
      <SwiperBox>
        <IconLeft id="icon-before">
          <img src={iconL} alt="上一页"></img>
        </IconLeft>
        <IconRight id="icon-next">
          <img src={iconR} alt="下一页"></img>
        </IconRight>
        <NewsBack>动态</NewsBack>
        <div className="news-swiper-container swiper-container" style={{width: '100%'}}>
          <div className="swiper-wrapper">
            {
              news.map((item, index) => {
                return (
                  <div
                    style={{paddingRight: '.22rem', width: '.33rem'}}
                    key={index}
                    className="swiper-slide">
                    <img src={item.img} alt="news"></img>
                    <NewsMsgWrapper>
                      <NewsItemTitle>
                        { item.title }
                      </NewsItemTitle>
                      <NewsItemSub>{ item.sub }</NewsItemSub>
                      <NewsItemCon>{ item.content }</NewsItemCon>
                    </NewsMsgWrapper>
                  </div>
                )
              })
            }
          </div>
        </div>
      </SwiperBox>

    </NewsBox>
  )
}
