import React, { useState, useRef  } from 'react'
import styled from 'styled-components'
import './msg.css'
import msg from 'assets/image/msg.png'
import request from 'service'
import SelectBox from './selectBox'

export default function Msg() {
  const [name, setName] = useState('')
  const [phone, setPhone] = useState('')
  const [businessName, setBus] = useState('')
  const [show, setShow] = useState(false)
  const [scale, setScale] = useState('')
  const [toast, setToast] = useState('')
  const [showToast, setShowToast] = useState(false)

  const LeftTop = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    width: .35rem;
    height: .35rem;
    border-width: .07rem;
    border-style: solid;
    border-color: #00e1eb transparent transparent #00e1eb;
  `
  const LeftBottom = styled.div`
    position: absolute;
    bottom: 0;
    left: 0;
    width: .35rem;
    height: .35rem;
    border-width: .07rem;
    border-style: solid;
    border-color: transparent transparent #0ba1f8 #0ba1f8;
  `
  const RightTop = styled.div`
    position: absolute;
    top: 0;
    right: 0;
    width: .35rem;
    height: .35rem;
    border-width: .07rem;
    border-style: solid;
    border-color: #0ba1f8 #0ba1f8 transparent transparent;
  `
  const RightBottom = styled.div`
    position: absolute;
    bottom: 0;
    right: 0;
    width: .35rem;
    height: .35rem;
    border-width: .07rem;
    border-style: solid;
    border-color: transparent #00e1eb #00e1eb transparent;
  `

  
  const openToast = (val) => {
    setToast(val)
    setShowToast(true)
  }
  const hideToast = () => {
    setToast('')
    setShowToast(false)
  }
  const handleClick = () => {
    if (!name || !phone || !businessName || !scale) {
      openToast('请补充信息')
      setTimeout(hideToast, 1500)
      return;
    }
    let form = {
      name,
      phone,
      businessName,
      scale,
      receiveMail: 'guanjunhui@zhengjiamax.com',
    }
    request.post('/official/sendMail', {
      data: form,
    }).then(res => {
      console.log(res)
    }).catch(err => {
      console.log(err)
    })
  }
  const onChangeName = (e) => setName(e.target.value)
  const onChangePhone = (e) => setPhone(e.target.value)
  const onChangeBus = (e) => setBus(e.target.value)
  return (
    <div
      style={{
        position: 'relative',
        paddingBottom: `calc(1080 / 1920 * 100%)`,
        'background': `url(${msg}) no-repeat`,
        'background-size': `100%`,
      }}
    >
      <div className="msg-input-box">
        <div className="msg-title">即刻拥有您的专属解决方案</div>
        <input value={name} onChange={onChangeName} className="msg-input-item" placeholder="您的称呼"></input>
        <input value={phone} onChange={onChangePhone} className="msg-input-item" placeholder="您的联系方式"></input>
        <input value={businessName} onChange={onChangeBus}  className="msg-input-item" placeholder="您所在的企业名称"></input>
        <SelectBox scale={scale} show={show} setScale={setScale} setShow={setShow}></SelectBox>
        <div className="msg-btn" onClick={handleClick}>提交</div>
        <LeftTop></LeftTop>
        <LeftBottom></LeftBottom>
        <RightTop></RightTop>
        <RightBottom></RightBottom>
      </div>
      {
        showToast == false ? null : <div className="toast">{ toast }</div>
      }
    </div>
  )
}
