import React, { useState } from 'react'
import logo from 'assets/image/logo.png'
import './navbar.css'

export default function Navbar() {
  const list = [
    { title: '首页' },
    { title: '解决方案' },
    { title: '新闻中心' },
    { title: '关于我们' },
  ]
  const [active, changeActie] = useState(0)
  
  return (
    <div className="navbar-box">
      <div className="navbar-logo">
        <img src={logo}></img>
      </div>
      <div className="navbar-list">
        {
          list.map((item, index) => {
            return (
              <div
                onClick={() => changeActie(index)}
                key={index} 
                className={"navbar-item" + (active === index ? ' active' : '')}>
                { item.title }
              </div>
            )
          })
        }
      </div>
    </div>
  )
}
