import React from 'react'
export default function Box({ scale, show, setScale, setShow }) {
  const changeScale = (val) => {
    setScale(val)
    setShow(false)
  }
  const scaleList = ['小型', '中小型', '大型', '特大型']
  return (
    <>
      <div className="msg-select-item" onClick={() => {setShow(!show)}}>
        { scale === '' ? '请选择企业规模' : scale }
        <div className="msg-select-icon"></div>
      </div>
      {
        show == false ? null : 
        <div className="select-box">
          {
            scaleList.map((item, index) => {
              return (
                <div className="select-item" key={index} onClick={() => { changeScale(item) }}>{ item }</div>
              )
            })
          }
        </div> 
      }
    </>)
}
