import React from 'react'
import { BottomBox, BottomImg, BottomLink, BottomTitle, BottomWrapper, WrapperItem } from './styled'
import logo2 from 'assets/image/logo2.png'

export default function Bottom() {

  const onClickLink = (link) => {
    window.open(link)
  }

  return (
    <BottomBox>
      <BottomWrapper>
        <WrapperItem>
          <BottomTitle>产品</BottomTitle>
          <BottomLink>灵活用工结算</BottomLink>
          <BottomLink>虎牙业务</BottomLink>
        </WrapperItem>
        <WrapperItem>
          <BottomTitle>案例</BottomTitle>
          <BottomLink>查看案例</BottomLink>
          <BottomLink>我的案例</BottomLink>
          <BottomLink>更多案例查看</BottomLink>
        </WrapperItem>
        <WrapperItem>
          <BottomTitle>关于</BottomTitle>
          <BottomLink>新闻中心</BottomLink>
          <BottomLink>企业简介</BottomLink>
          <BottomLink>更多案例查看</BottomLink>
        </WrapperItem>
        <BottomImg>
          <img src={logo2} alt="logo"></img>
        </BottomImg>
      </BottomWrapper>
    </BottomBox>
  )
}
