import React, { useEffect } from 'react'
import Swiper, {Autoplay } from 'swiper'
import 'swiper/swiper-bundle.css'

import banner1 from 'assets/image/banner1.png'
import search from 'assets/image/search.png'
import banner2 from 'assets/image/banner2.png'
import playBtn from 'assets/image/play.png'

import {
  LgBlock,
  LgSub,
  LgTitle,
  LgDesc,
  LgWrapper,
  LgBtn,
  LgInput,
  LgInputWrapper,
  LgSearch,
  Huya,
  HuyaTitle,
  HuyaDesc,
  HuyaBtnBox,
  HuyaBtn,
  HuyaPlayBtn,
  HuyaPlay,
} from './styled.js'

Swiper.use([Autoplay])
export default function Banner() {
  useEffect(() => {
    new Swiper('.banner-swiper', {
      autoplay: true,
      loop: true
    })
  }, [])

  return (
    <div className="banner-box">
      <div className="banner-swiper swiper-container">
        <div className="swiper-wrapper">
          <div className="swiper-slide">
            <img src={banner1} alt="banner"></img>
            <LgBlock>
              <LgSub>实现科技赋能，人才真共享</LgSub>
              <LgTitle>劳动力解决方案科技平台</LgTitle>
              <LgDesc>正佳科技集团旗下子公司 · 汇聚支付兄弟企业 ·3000+企业共同选择</LgDesc>
              <LgWrapper>
                <LgBtn>查看详情</LgBtn>
                <div style={{position: 'relative', lineHeight: 0, marginTop: '1.47rem'}}>
                  <LgInputWrapper>
                    <LgInput placeholder='输入你想要搜索的内容'></LgInput>
                    <LgSearch>
                      <img src={search} alt='search'></img>
                    </LgSearch>
                  </LgInputWrapper>
                </div>
              </LgWrapper>
            </LgBlock>
          </div>
          <div className="swiper-slide">
            <img src={banner2} alt="banner"></img>
            <Huya>
              <HuyaTitle>虎牙直播官方授权</HuyaTitle>
              <HuyaDesc>虎牙礼包现正火热发售，欢迎各大公会接洽合作</HuyaDesc>
              <HuyaBtnBox>
                <HuyaBtn>立即购买</HuyaBtn>
                <HuyaPlay>
                  <HuyaPlayBtn>
                    <img src={playBtn} alt="play"></img>
                  </HuyaPlayBtn>
                  了解更多</HuyaPlay>
              </HuyaBtnBox>
            </Huya>
          </div>
        </div>
      </div>
    </div>
  )
}
